import React from 'react';
import { OrderStatus } from 'store/constants/orderStatus';
import {
    DialogPagePortal,
    DialogTitlePortal,
    DialogButtonPortal,
    DialogTextPortal,
} from 'components/DialogPage/DialogPage@portal';
import { SpinnerPage } from 'components/SpinnerPage/SpinnerPage';
import { MultilineText } from 'components/MultilineText/MultilineText';
import { ButtonViews } from 'components/Button/Button';

import { useSummaryPage } from './StatusSummary.hooks';
import { StatusSummaryDecoration } from './Decoration/StatusSummaryDecoration';

const mapOrderStatusToSummaryText: Record<OrderStatus, string[]> = {
    [OrderStatus.INITIAL]: [''],
    [OrderStatus.INVOICE_CREATED]: [' ,\n   '],
    [OrderStatus.FUNDS_RESERVED]: [' \n  '],
    [OrderStatus.CONFIRMED_BY_PARTNER]: [' .\n  \n   '],
    [OrderStatus.DELIVERING]: ['  .\n  \n   '],
    [OrderStatus.DELIVERED]: [' \n '],
    [OrderStatus.CANCEL_BY_PARTNER]: [' , \n '],
    [OrderStatus.CANCEL_BY_CUSTOMER]: [' , \n '],
    [OrderStatus.CANCEL_BY_PAYMENT]: [' , \n '],
    [OrderStatus.PAID]: [' \n '],
    [OrderStatus.CANCELLED]: [' , \n '],
    [OrderStatus.ERROR]: [' \n '],
};

export const StatusSummaryPortal: React.FC = () => {
    const {
        orderStatus,
        orderStatusCategory,
        isLoaded,
        isFailed,
        handleDetailsClick,
        handleReportClick,
    } = useSummaryPage();

    const [title, text] = React.useMemo(() => mapOrderStatusToSummaryText[orderStatus], [orderStatus]);

    const buttons: React.ReactNode = React.useMemo(() => {
        const result = [
            <DialogButtonPortal shouldFocusOnMount={!isFailed} key="dialog-button-details" onClick={handleDetailsClick}>
                
            </DialogButtonPortal>,
            <DialogButtonPortal
                shouldFocusOnMount={isFailed}
                key="dialog-button-report"
                view={isFailed ? ButtonViews.primary : ButtonViews.secondary}
                onClick={handleReportClick}
            >
                  
            </DialogButtonPortal>,
        ];

        return isFailed ? result.reverse() : result;
    }, [handleDetailsClick, handleReportClick, isFailed]);

    const decorations = <StatusSummaryDecoration orderStatusCategory={orderStatusCategory} />;

    if (!isLoaded) {
        // TODO:    https://sbtatlas.sigma.sbrf.ru/jira/browse/FOODTECHUI-243
        return <SpinnerPage text="  " />;
    }

    return (
        <DialogPagePortal buttons={buttons} decorations={decorations}>
            <DialogTitlePortal>
                <MultilineText text={title} />
            </DialogTitlePortal>
            {text ? (
                <DialogTextPortal>
                    <MultilineText text={text} />
                </DialogTextPortal>
            ) : null}
        </DialogPagePortal>
    );
};
