import React from 'react';
import {
    DialogPageSberbox,
    DialogTitleSberbox,
    DialogButtonSberbox,
    DialogTextSberbox,
} from 'components/DialogPage/DialogPage@sberbox';
import { MultilineText } from 'components/MultilineText/MultilineText';

import { useRenderErrorPage } from './RenderError.hooks';

export const RenderErrorSberbox: React.FC = () => {
    const { handleCloseApp, handleGoBack } = useRenderErrorPage();

    const buttons = React.useMemo(
        () => (
            <>
                <DialogButtonSberbox shouldFocusOnMount onClick={handleGoBack}>
                     
                </DialogButtonSberbox>
                <DialogButtonSberbox onClick={handleCloseApp}> </DialogButtonSberbox>
            </>
        ),
        [handleCloseApp, handleGoBack],
    );

    return (
        <DialogPageSberbox buttons={buttons}>
            <DialogTitleSberbox>
                <MultilineText text="-   " />
            </DialogTitleSberbox>
            <DialogTextSberbox>
                <MultilineText text=",   " />
            </DialogTextSberbox>
        </DialogPageSberbox>
    );
};
