import { catalogListScrollSpeed } from 'pages/Catalog/Catalog.const/common';
import React from 'react';
import { catalogPortalListScrollAction } from 'store/actions/catalogPage';
import { layout } from 'style/const/layout@portal';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import Swiper from 'swiper';
import { useDispatch } from 'hooks/useDispatch';
import 'swiper/swiper-bundle.css';

import { CatalogListItemPortal, cardHeightPx, cardWidthPx } from '../ListItem/CatalogListItem@portal';

export const catalogCardGap = 32;

const StyledRoot = styled.div`
    position: relative;

    height: ${layout.contentHeight}px;
    padding-top: 32px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    justify-content: start;

    overflow: hidden;
`;

const StyledWrapper = styled.div`
    height: ${cardHeightPx}px;
    //    -   ,    
    //         
    width: max-content;
`;

const StyledSwiperSlide = styled.div`
    width: ${cardWidthPx}px;

    // swiper -        
    //      .   ,   .
    transform: none !important;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const preventAllScroll = (e: React.UIEvent<HTMLDivElement>) => {
    e.currentTarget.scrollTop = 0;
    e.currentTarget.scrollLeft = 0;
};

export const CatalogListPortal: React.FC = () => {
    const ref = React.useRef<HTMLDivElement>(null);
    const swiperRef = React.useRef<Swiper | null>(null);
    const dispatch = useDispatch();
    const order = useSelector(({ catalogPage }) => catalogPage.order);
    const listIndexOffset = useSelector(({ catalogPage }) => catalogPage.listIndexOffset);

    /**
     *     useLayoutEffect - , 
     *          .
     * ,      ,   
     *         .
     */
    React.useLayoutEffect(() => {
        if (ref.current instanceof HTMLDivElement) {
            swiperRef.current =
                swiperRef.current ||
                new Swiper(ref.current, {
                    slidesPerView: 'auto',
                    speed: catalogListScrollSpeed,
                    spaceBetween: catalogCardGap,
                    initialSlide: listIndexOffset,
                    roundLengths: true,
                    /**
                     *        
                     *     .
                     */
                    normalizeSlideIndex: false,
                    slidesOffsetBefore: layout.contentPaddingHorizontal,
                    slidesOffsetAfter: layout.contentPaddingHorizontal,
                    on: {
                        slideChange: () => {
                            if (swiperRef.current) {
                                //    swiper'
                                dispatch(catalogPortalListScrollAction(swiperRef.current.activeIndex));
                            }
                        },
                    },
                });
        }
    }, [dispatch, listIndexOffset]);

    /**
     *  swiper  .      
     * useLayoutEffect.       
     *    DOM (    
     *     ),
     *      .
     */
    React.useLayoutEffect(() => {
        if (swiperRef.current) {
            swiperRef.current.slideTo(listIndexOffset, catalogListScrollSpeed);
        }
    }, [listIndexOffset]);

    const wrapper = React.useMemo(() => {
        return (
            <StyledWrapper className="swiper-wrapper">
                {order.map((itemId) => (
                    <StyledSwiperSlide key={itemId} className="swiper-slide">
                        <CatalogListItemPortal id={itemId} />
                    </StyledSwiperSlide>
                ))}
            </StyledWrapper>
        );
    }, [order]);

    return (
        <StyledRoot tabIndex={0} onScroll={preventAllScroll} ref={ref} className="swiper-container">
            {wrapper}
        </StyledRoot>
    );
};
